import React from "react";

class Application extends React.Component{

    render(){
        return <article className="col-md-6 work-item">
            <div className="image fit">
                <img src={this.props.imageUrl} alt="" />
                <div className="overlay">
                    <div className="overlayButtons">
                        <a className="butn codeButn" href={this.props.detailUrl} target="_blank">
                            Details
                        </a>{' '}
                        <a className="butn appButn" href={this.props.demoUrl} target="_blank">
                            App
                        </a>{' '}
                    </div>
                </div>
            </div>
            <h3>{this.props.appName}</h3>
            <p>{this.props.techStack}</p>
        </article>
    }

}

export default Application;