import React from "react";
import Typed from 'react-typed';

class Header extends React.Component{

    constructor(props){
        super(props)
        this.state={
            name:"Binish Manandhar ",
            typed:"_",
        }
    }

    componentDidMount(){
        let stringArray = this.state.name.split('');
        let durationInterval=90;
        stringArray.forEach((char,index)=>{
            setTimeout(()=>{
                let typedArray = this.state.typed.split("");
                let length = typedArray.length;
                typedArray.splice(length-1,0,char);
                this.setState({
                    typed :typedArray.join(""),
                })
            },durationInterval);
            durationInterval=durationInterval+90;
            console.log(durationInterval);
            
        });
    }

    render(){
        return <header id="header">
                <div className="inner">
                    <h1><strong>
                        <Typed
                            strings={[this.state.name]}
                            typeSpeed={50}
                        />
                    </strong></h1>
                    <b>Software Engineer</b>
                    <div className="customNav">
                        <ul>
                            <li><a href="#" target="#">Front-End </a></li>
                            <li><a href="#">Full Stack</a></li>
                            <li><a href="#">App Development</a></li>
                            <li><a href="#">Learning</a></li>
                        </ul>
                    </div>
                </div>
            </header>
    }
}

export default Header;